conveniosmarcootrosiService.getDataXConvenio = function(convenio){

    return new Promise(function(resolve, reject) {axios({
        method:'POST',
        url: configService.apiUrl + '/OtroSiXConvenioMarco',
        dataType: "json",
        data:{
          convenio:convenio
        }        
      })
      .then(function(res) {

        resolve(res)      
        
      })
      .catch(function(err) {     

        resolve(err)
           
      })
    })

  }

  conveniosmarcootrosiService.guardarData = function(data){

    return new Promise(function(resolve, reject) {axios({
        method:'POST',
        url: configService.apiUrl + '/addupdConvenioMarcoOtrosi',
        dataType: "json",
        data: {
          data: data         
        }
      })
      .then(function(res) {

        resolve(res)      
        
      })
      .catch(function(err) {     

        resolve(err)
           
      })
    })

  }  

  conveniosmarcootrosiService.DelData = function(Codigo,convenio){

    return new Promise(function(resolve, reject) {axios({
        method:'POST',
        url: configService.apiUrl + '/delConvenioMarcoOtrosi',
        dataType: "json",
        data: {
            codigo: Codigo,
            convenio:convenio          
        }
      })
      .then(function(res) {

        resolve(res)      
        
      })
      .catch(function(err) {     

        resolve(err)
           
      })
    })

  }

  export default conveniosmarcootrosiService