        },

        traerDataConvenio(){

            var _this = this
           
            _this.conveniomarco = ''
            _this.nombreconvenio = ''           

            conveniosmarcoService.getDataXId(this.convenio)
                .then(function(res) {

                    if (res.request) {
                                    
                        if(res.data){

                            _this.conveniomarco = res.data.data[0].codigo
                            _this.nombreconvenio = res.data.data[0].nombreasociado                           
                            
                        }

                    }
                    
                    _this.loading = false                
                   
                    
            }).catch(function(err) {

                    
            })                

        },

        traerdata(convenio){

            var _this = this
            this.loading = true

            conveniosmarcootrosiService.getDataXConvenio(convenio)
                .then(function(res) {

                    if (res.request) {
                                    
                        if(res.data){

                            _this.data = res.data.data 

                        }  else {
                            
                            _this.data = []
                        
                        }

                    } else {                        	

                        _this.data = []    
                                                           	
                    }    
                    _this.loading = false                
                    
            }).catch(function(err) {

                    _this.data = []

                    _this.loading = false                
                
            })

        },

        deleteItem(){
            
