            this.loading = true            

            conveniosmarcootrosiService.DelData(_this.itemactual.codigo, _this.convenio)
                .then(function(res) {

                    if(res.statusText){

                        _this.showMessage(true,3000,'success','Se eliminó el Otro Si con éxito')

                        _this.del_item = ''	              	  		
                        _this.traerdata(_this.convenio)
