                                                ', FECHA DE INCORPORACION:' + _this.itemactual.fechainicio + 
                                                ', FECHA TERMINACION: ' +  _this.itemactual.fechaterminacion + 
                                                '. QUE PERTENECE AL CONVENIO MARCO: ' + _this.conveniomarco + ' Y NOMBRE ASOCIADO: ' + 
                                                _this.nombreconvenio)

                        /*var mensajecorreo = "Cordial Saludo,<br><br> " +
                                            "Se eliminó el Otro Si con Fecha de Inicio : '" + _this.itemactual.fechasuscripcion + 
                                            "', Fecha de Incoprporación: '" + _this.itemactual.fechainicio +
                                            "', Fecha de Terminación Ejecucion: '" + _this.itemactual.fechaterminacion +
                                            "', Valor Total: '" + _this.itemactual.valortotal + 
                                            "' del # de Convenio " + 
                                            _this.conveniomarco + ", Nombre: " + _this.nombreconvenio +
                                            "<br><br>Agradeciendo su atención,<br><br> " +                        
                                            "<b>SINCHI</b><br><br> " +
                                            "<i>Este es un servicio de notificaciones generado automáticamente por medio de " +
                                            "correo electrónico, no lo responda.</i> <br>"
                        
                        var mensajenotifica = "Se eliminó el Otro Si con Fecha de Inicio : '" + _this.itemactual.fechasuscripcion + 
                                            "', Fecha de Incoprporación: '" + _this.itemactual.fechainicio +
                                            "', Fecha de Terminación Ejecucion: '" + _this.itemactual.fechaterminacion +
                                            "', Valor Total: '" + _this.itemactual.valortotal +  
                                            "' del # de Convenio " + 
                                            _this.conveniomarco + ", Nombre: " + _this.nombreconvenio

                        _this.envioNotificaciones(mensajenotifica,'Eliminación de otro si de Convenio',mensajecorreo)*/                                 

                    } else {	                                         	

                        _this.showMessage(true,3000,'error',res.response.data.message) 	                  	
                    } 	                   
                    _this.del_item = ''						
                    _this.loading = false	                 
                    
            }).catch(function(err) {

                _this.showMessage(true,3000,'error',err)	    
