            })

        },        

        validaSave(){

            if(this.itemactual.fechasuscripcion == this.fechasuscripcion && this.itemactual.fechaterminacion == this.fechaterminacion &&
                this.itemactual.fechainicio == this.fechainicio){

                return false

            } else {

                return true

            }	
        },
            
        saveitem(){            
            
            this.loading = true           
            var _this = this

            var datbit = ''

            var data = {codigo:this.codigo,convenio:this.convenio,fechasuscripcion:this.fechasuscripcion, 
